/* Buttons
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.button, button {
 display: inline-block;
 text-align: center;
 letter-spacing: .1rem;
 text-transform: uppercase;
 text-decoration: none;
 white-space: nowrap;
 background-color: transparent;
 border-radius: 4px;
 border: 5px solid transparent;
 cursor: pointer;
 box-sizing: border-box;
 margin: 1rem;
 background-color: rgba(230, 46, 104, 0.7);


 @include size(40px);
 line-height: 40px;
 vertical-align: middle;
 img {
 @include size(30px);
 padding: 5px; } }

@media screen and (min-width: 768px) {
 .button, button {
 margin-top: 3rem;
 @include size(50px);
 line-height: 50px;
 img {
 @include size(40px); } } }


.button:hover, button:hover {
 border-bottom-color: transparent;
 background-color: rgba(230, 46, 104, 1); }
