/* Media
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

img:not([src]):not([srcset]) {
 visibility: hidden; }

@-moz-document url-prefix() {
 img:-moz-loading {
 visibility: hidden; } }

img,
video,
object {
 max-width: 100%;
 height: auto;
 border: none;
 outline: none; }

figure {
 max-width: 100%;
 margin: auto; }

figcaption {
 margin: 2rem auto;
 text-align: center;
 @include font(false, false, false, false, 3);
 border: 1px solid #000000;
 background-color: #fafafa; }
