/* Base styles
 ** Table of contens:
 *
 *- sunset: #e62e68 in background-color: rgba(230, 46, 104, 0.7)
 *- sunshine: #e66e2e
 *-
 *- background-color: rgba(38,57,89,0.6) #263959
 *
 *- NOTE: html is set to 62.5% so that all the REM measurements throughout Skeleton
 *- are based on 10px sizing. So basically 1.5rem = 15px :)
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

$grey050: #fafafa;
$grey500: #9e9e9e;
$grey700: #616161;
$white : #ffffff;
$black : #000000;
$text: #222222;
$clean: #dfdce3;

$sunset: rgba(230, 46, 104, 1);
$sunshine: rgba(230, 110, 46, 1);
$fresh: rgba(200, 231, 179, 1);
$sea: rgba(147, 219, 244, 1);
$sun: rgba(255, 235, 39, 1);

//$systemFF: Raleway, HelveticaNeue, Helvetica, Arial, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif
$systemFF: sans-serif, serif, monospace, fantasy, cursive;

*,
*::before,
*::after {
 box-sizing: border-box; }

html {
 -webkit-text-size-adjust: 100%;
 -ms-text-size-adjust: 100%;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 font-variant-ligatures: common-ligatures;
 -webkit-tap-highlight-color: transparent;
 font-kerning: normal;
 font-family: $systemFF;
 font-size: 62.5%;
 box-sizing: border-box; }

html.no-js {
 body:before {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 content: "Contact me at rotokyo@icloud.com"; } }

body {
 -webkit-text-size-adjust: 100%;
 -webkit-font-smoothing: antialiased;
 font-kerning: normal;
 font-variant-ligatures: common-ligatures;
 text-rendering: optimizeLegibility;
 overflow-x: hidden;
 /* currently ems cause chrome bug misinterpreting rems on body element */
 @include font($systemFF,1.6rem,$text,400,1.6);
 margin: 0;
 min-height: 100vh;
 scroll-behavior: smooth; }



.bodybgk {
 @include backgroundImage(false,false);
 height: 100vh;
 background-image: url("https://res.cloudinary.com/dpr6frnpl/image/upload/f_auto,q_auto,w_415/v1584944760/bc/bgk_portrait.jpg"); }
@media screen and (orientation: landscape) and (min-width: 568px) {
 .bodybgk {
 background-image: url("https://res.cloudinary.com/dpr6frnpl/image/upload/f_auto,q_auto,w_568/v1584944754/bc/bgk_landscape.jpg"); } }
@media screen and (orientation: landscape) and (min-width: 812px) {
 .bodybgk {
 background-image: url("https://res.cloudinary.com/dpr6frnpl/image/upload/f_auto,q_auto,w_812/v1584944754/bc/bgk_landscape.jpg"); } }
@media screen and (min-width: 1024px) {
 .bodybgk {
 background-image: url("https://res.cloudinary.com/dpr6frnpl/image/upload/v1584944754/bc/bgk_landscape.jpg"); } }
