/* Section Hero
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.sectionHero {
 margin: 0 auto;
 text-align: center;
 @include cv; }

.bannerTitle {
 @include font(sans-serif,false,$text,700,false);
 white-space: nowrap;
 letter-spacing: 0.3rem;
 margin-top: 4rem; }

hr {
 display: none;
 visibility: hidden;
 margin: -1.7rem auto 1rem;
 max-width: 70%;
 border : 0;
 height: 1px;
 background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0)); }

.vCard {
 margin: 0 auto;
 @include size(12rem); }

@media screen and (orientation: landscape) {
 .bannerTitle {
 margin-top: 6rem; }
 .vCard {
 margin-top: -2rem;
 } } //display: none

@media screen and (min-width: 1024px) {
 .bannerTitle {
 @include font(false, 5.0rem, false, false, 1.1); }
 hr {
 display: block;
 visibility: visible;
 margin-top: 3rem; }
 .vCard {
 display: block;
 margin-top: 3rem;
 @include size(12rem); } }
